@import "./utils/variables";
@import "./utils/mixins";
@import "./blocks/header";
@import "./blocks/top-bar";
@import "./blocks/btn";
@import "./blocks/menu";
@import "./blocks/nav";
@import "./blocks/section-title";
@import "./blocks/main";
@import "./blocks/about-us";
@import "./blocks/cards";
@import "./blocks/details";
@import "./blocks/gallery";
@import "./blocks/contacts";
@import "./blocks/footer";

* {
  box-sizing: border-box;
}

html {
  font-family: Poppins, sans-serif;
  scroll-behavior: smooth;

  &:has(.menu:target) {
    overflow: hidden;
  }
}

body {
  margin: 0;
  background-color: $color-main-bg;
  color: $color-main-text;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
}

input,
textarea,
button {
  border: none;
  outline: none;
}

.container {
  @include padding-inline;
}
